import React from 'react'
import PropTypes from 'prop-types'

export default function Section({ title, content: Content, dark, id }) {
  return (
    <div className={`section${dark ? ' section-dark' : ''}`}>
      <div className="section-content" id={id}>
        <h1>{title}</h1>

        {Content && <Content />}
      </div>
    </div>
  )
}

const { string, object, node, oneOfType, bool } = PropTypes
Section.propTypes = {
  title: string.isRequired,
  content: oneOfType([node, object]),
  dark: bool,
  id: string.isRequired,
}

Section.defaultProps = {
  dark: false,
  content: undefined,
}
