import React, { Component } from 'react'
import './site.css'
import Navbar from '../components/navbar'
import Section from '../Components/section'

import Home from '../content/home'
import EyeCareServices from '../content/eye-care'
import Laboratory from '../content/laboratory'
import AboutUs from '../content/aboutUs'
import ContactUs from '../content/contactUs'

class App extends Component {
  render() {
    return (
      <div className="App">
        <Navbar />
        <Section title="Welcome to Gliane Optical" content={Home} dark id="home" />
        <Section title="Eye care Services" content={EyeCareServices} dark id="eyeCareServices" />
        <Section title="Laboratory" content={Laboratory} dark={false} id="laboratory" />
        <Section title="About Us" content={AboutUs} dark id="aboutUs" />
        <Section title="Contact Us" content={ContactUs} dark={false} id="contactUs" />
      </div>
    )
  }
}

export default App
